	import amap from '../../common/utils/amap-wx.js';  
	
	export default {
		data() {
			return {
				title: 'Hello',
				address: '',
				amapPlugin: null,  
        key: 'afc6e09bc51c1c6deb31a734c69e5125',
				addressName: '定位中...',  
				weather: {  
					hasData: false,  
					data: []  
				},
				// 上门日期选择
				days: ['今天', '明天', '后天'],
				times: ['10点-11点', '12点-13点', '14点-15点'],
				timeSelect: [0,0],
				/**
				 * 解决动态设置indicator-style不生效的问题
				 */
				visible: false,
				indicatorStyle: `height: ${Math.round(uni.getSystemInfoSync().screenWidth/(750/100))}px;`
			}
		},
		onLoad() {
			this.amapPlugin = new amap.AMapWX({  
					key: this.key  
			});  
			// this.getRegeo
			this.amapPlugin.getRegeo({  
					success: (data) => {  
							console.log(data)  
							this.addressName = data[0].regeocodeData.aois && data[0].regeocodeData.aois.length > 0 ? data[0].regeocodeData.aois[0].name : '暂时无法获取您的位置';  
							uni.hideLoading();  
					},
					fail: (err) => {
						console.log(err)
					}
			});  
		},
		methods: {
			handleTimePicker() {
				this.visible = true
			},
			// 时间选择
			bindChange (e) {
				console.log(e, e.detail.value)
			    const val = e.detail.value
			    // this.year = this.years[val[0]]
			    // this.month = this.months[val[1]]
			    // this.day = this.days[val[2]]
			},
			// 高德地图
			getRegeo() {  
				uni.showLoading({  
						title: '获取信息中'  
				});  
				this.amapPlugin.getRegeo({  
						success: (data) => {  
								console.log(data)  
								this.addressName = data[0].name;  
								uni.hideLoading();  
						}  
				});  
			} 
		}
	}
